.fullTask
    display: flex
    flex-direction: column
    padding: 0 5px 5px 5px
    max-height: 80dvh
    overflow: hidden
    .fullTask_header
        display: flex
        align-items: center
        justify-content: space-between
        padding: 0 15px
        .fullTask_button_cancel
            background-color: #55ef88
    .fullTask_scroll
        display: flex
        flex-direction: column
        gap: 5px
        overflow: auto
        .summary,.description
            display: flex
            flex-direction: column
            background-color: #fff
            border-radius: 5px
            padding: 10px
            gap: 5px
            .summary_title, .description_title
                border-bottom: 1px solid #0000005a
                padding-bottom: 5px
                text-align: center
